<template>
  <div class="contact-us">
    <v-img
      :src="require('../assets/images/banner_image3.jpg')"
      aspect-ratio="4"
      gradient="to top, #FFE52D, rgba(1,1,1,0)"
    ></v-img>
    <div style="padding: 5vmin 10vmin 5vmin 10vmin;" class="white">
      <p :class="changeDescClasses + ' font-weight-bold text-center ma-0'">
        Contact us today to learn more about how communications can help you make a difference
      </p>
    </div>
    <div style="padding: 5vmin 10vmin 5vmin 10vmin;" class="primary">
      <v-row no-gutters="" align="center" justify="center">
        <v-col xl="8" lg="9" md="10" sm="11" cols="12">
          <p :class="bodyClasses + ' font-weight-bold text--darken-4'">
            Fill out the form below to email us:
          </p>
          <v-textarea solo flat auto-grow rows="5">
          </v-textarea>

          <v-text-field solo flat label="Subject of inquiry..."></v-text-field>

          <p :class="bodyClasses + ' font-weight-bold text--darken-4'">
            Full Name
          </p>
          <v-text-field solo flat></v-text-field>

          <p :class="bodyClasses + ' font-weight-bold text--darken-4'">
            Email
          </p>
          <v-text-field solo flat></v-text-field>

          <v-checkbox
            color="primary lighten-2"
            label=""
          >
            <template v-slot:label>
              <span :class="bodyClasses + ' font-weight-bold text--darken-4'">
                Send me tools, tips, and periodic email updates.
              </span>
            </template>
          </v-checkbox>

          <v-row no-gutters="" align="center" justify="center">
            <v-btn
              text large
              class="font-weight-medium grey--text text--darken-4 title font-italic"
            >
              <span class="text-lowercase">SEND MESSAGE ></span>
            </v-btn>
          </v-row>
        </v-col>
      </v-row>
    </div>
    <div style="padding: 5vmin 10vmin 5vmin 10vmin;" class="white">
      <p :class="changeDescClasses + ' font-weight-medium text-center ma-0'">
        Change Consulting, LLC
        <br/>
        101 Broadway, Suite 202
        <br/>
        Oakland, CA 94607
      </p>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    changeDescClasses() {
      const baseClasses = 'grey--text text--darken-4';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-1`;
        case 'sm':
          return `${baseClasses} title`;
        case 'md':
          return `${baseClasses} headline`;
        case 'lg':
          return `${baseClasses} display-1`;
        default:
          return `${baseClasses} display-1`;
      }
    },

    bodyClasses() {
      const baseClasses = 'grey--text';

      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return `${baseClasses} body-2`;
        case 'sm':
          return `${baseClasses} body-1`;
        case 'md':
          return `${baseClasses} title`;
        case 'lg':
          return `${baseClasses} title`;
        default:
          return `${baseClasses} title`;
      }
    },
  },
};
</script>
